<template>
  <div id="force" style="width: 100vw; height: 100vh" @click="menu.isShow = false"></div>
  <div class="el-select-dropdown__wrap el-scrollbar__wrap el-scrollbar__wrap--hidden-default" v-show="menu.isShow"
    :style="{ left: menu.x, top: menu.y }">
    <ul class="el-scrollbar__view el-select-dropdown__list" style="">
      <li class="el-select-dropdown__item">
        <span class="demonstration">选择颜色</span>
        <el-color-picker :value="color" @change="changeColor" />
      </li>
      <li class="el-select-dropdown__item" @click="hiddenChildren">
        <span class="demonstration">{{ d.hiddenChildren ? '展开所有子节点' : '收起所有子节点' }}</span>
      </li>
      <li class="el-select-dropdown__item" @click="switchShowLink">
        <span class="demonstration">{{ d.toShowOnly ? '显示所有节点' : '只显示当前关联节点' }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import CForce from './c-force'
import { json, hsl } from 'd3'

import { set16ToRgb, getId, data } from './config'

export default {
  name: 'App',
  data () {
    return {
      color: '#00fff0',
      menu: {
        isShow: false,
        x: 0,
        y: 0,
      },
      d: {}
    }
  },
  methods: {
    changeColor (v) {
      const { h, s, l, opacity } = hsl(v)
      var color = {
        innerFill: hsl(h, s, l * 0.7, opacity).rgb().toString(),
        innerStroke: v,
        outerFill: hsl(h, s, l * 0.3, opacity).rgb().toString(),
        outerStroke: hsl(h, s, l * 0.7, opacity).rgb().toString(),
      }
      this.d.color = color
      this.menu.isShow = false
      this.graf.updateNodes()
      this.menu.isShow = false
    },
    hiddenChildren () {
      const { d } = this
      d.hiddenChildren = !d.hiddenChildren
      const linksId = d.links().map(l => {
        return l.target.id + '__' + l.source.id
      })
      this.graf.links.forEach(link => {
        if (linksId.includes(link.target.id + '__' + link.source.id)) {
          link.hidden = d.hiddenChildren
        }
      })
      d.descendants().forEach(node => {
        // 排除自己
        if (node.id !== d.id) {
          node.hidden = d.hiddenChildren
        }
      })
      this.graf.updateNodes()
      this.graf.updateLinks()
      this.menu.isShow = false
    },

    switchShowLink () {
      const { d, graf } = this
      if (d.toShowOnly) {
        graf.nodes.forEach(node => {
          node.showOnly = false
          delete node.toShowOnly
        })
        graf.links.forEach(link => {
          link.showOnly = false
        })
      } else {
        const ancestors = d.ancestors().map(item => item[graf._node.key])
        // 如果是根节点，不允许操作
        if (d.id !== ancestors[ancestors.length - 1]) {
          const descendants = d.descendants().map(item => item[graf._node.key])
          const nodeIds = descendants.concat(ancestors)
  
          graf.nodes.forEach(node => {
            node.showOnly = !nodeIds.includes(node[graf._node.key])
            node.toShowOnly = true
          })
  
          const ancestorLinks = []
          for (var i = ancestors.length - 1; i > 0; i--) {
            ancestorLinks.push({
              source: { id: ancestors[i] },
              target: { id: ancestors[i - 1] }
            })
          }
          const nodeLinks = d.links().concat(ancestorLinks).map(({ source, target }) => source.id + '-' + target.id)
  
          graf.links.forEach(link => {
            const { source, target } = link
            link.showOnly = !nodeLinks.includes(source.id + '-' + target.id)
          })
        }
      }
      graf.updateNodes()
      graf.updateLinks()
      this.menu.isShow = false
    }
  },
  mounted () {
    var that = this
    json('./flare.json').then(data => {
      let length = sessionStorage.getItem('nodes') || 2
      this.graf = new CForce({
        el: '#force',
        data: { name: 'root', children: Array.from({ length }, () => data) },
        node: {
          onContextmenu (e, d, instance) {
            that.menu = {
              isShow: true,
              x: e.x + 'px',
              y: e.y + 'px',
            }
            that.d = d
          }
        }
      })
    })

    const gui = new dat.GUI();
    gui.addColor({ '选择颜色': '#00fff0' }, '选择颜色').onChange(v => {
      const { h, s, l, opacity } = hsl(v)
      var color = {
        innerFill: hsl(h, s, l * 0.7, opacity).rgb().toString(),
        innerStroke: v,
        outerFill: hsl(h, s, l * 0.3, opacity).rgb().toString(),
        outerStroke: hsl(h, s, l * 0.7, opacity).rgb().toString(),
      }
      this.graf.nodes.forEach(item => {
        item.color = color
      })

      this.graf.updateNodes()
    })
    gui.add({ '选择图形': 'M6.123233995736766e-16,-10A10,10,0,1,1,-6.123233995736766e-16,10A10,10,0,1,1,6.123233995736766e-16,-10Z' }, '选择图形', {
      '圆形': 'M6.123233995736766e-16,-10A10,10,0,1,1,-6.123233995736766e-16,10A10,10,0,1,1,6.123233995736766e-16,-10Z',
      '五角星': 'M 0,10 L3,4 L9,3.8 L4.8,-1.3 L6,-8 L0,-5 L-6,-8 L-4.8,-1.3 L-9,3.8 L-3,4Z',
      '正方形': 'M -7,7 L7,7 L7,-7 L-7,-7Z'
    }).onChange(v => {
      this.graf.nodes.forEach(item => item.path = v)
      this.graf.updateNodes()
    })
    gui.add({ '选择数据量': sessionStorage.getItem('nodes') || 2 }, '选择数据量', {
      '500条': 2,
      '1,000条': 4,
      '5,000条': 20,
      '10,000条': 40,
      '20,000条': 80,
      '50,000条': 200,
      '100,000条': 400,
      '200,000条': 800,
      '500,000条': 2000,
    }).onChange(v => {
      sessionStorage.setItem('nodes', v)
      location.reload()
    })
  }
}
</script>

