import { hierarchy, select, forceSimulation, zoom, zoomIdentity, forceCenter, forceManyBody, forceLink, forceCollide, event, hsl, path as d3Path, drag } from 'd3'
import {updateOpc} from './util'

export default class CForce {
  constructor({ el, data, node, link }) {
    this.$el = select(el)
    this._data = data
    this._node = Object.assign({
      key: 'id'
    }, node)
    this._link = Object.assign({
      key: 'id'
    }, link)
    this.transform = zoomIdentity.scale(1.2)
    this.initDom()
    this.initEvent()
    this.setData(data)
  }

  nodeProperty(d) {
    const { h, s, l, opacity } = hsl(d?.color || '#00fff0')
    return {
      r: 10,
      isDraw: true,
      hidden: false,
      opacity: 1,
      showOnly: false,
      color: {
        innerFill: hsl(h, s, l * 0.7, opacity).rgb().toString(),
        innerStroke: '#00fff0',
        outerFill: hsl(h, s, l * 0.3, opacity).rgb().toString(),
        outerStroke: hsl(h, s, l * 0.7, opacity).rgb().toString(),
      },
      id: this.getId(),
      path: 'M6.123233995736766e-16,-10A10,10,0,1,1,-6.123233995736766e-16,10A10,10,0,1,1,6.123233995736766e-16,-10Z'
    }
  }

  getLinkProperty() {
    return {
      isDraw: true,
      showOnly: false,
      hidden: false,
      opacity: 0.1,
      id: this.getId(),
      markEnd: 'url(#arrow-marker-end)',
      animation: false
    }
  }

  initEvent() {
    let firstBind = true
    this.$el.on('contextmenu', function() {
      event.preventDefault()
    }, true)
    const resizeObserver  = new ResizeObserver(() => this.resize(() => {
      if (firstBind) {
        firstBind = false
        return false
      }
      this.getDrawElement()
    }))
    resizeObserver.observe(this.$el.node())
  }

  resize(cb) {
    this.w = this.$el.node().clientWidth
    this.h = this.$el.node().clientHeight
    this.svg.attr('width', this.w).attr('height', this.h)
    cb?.()
  }

  setData (data) {
    let nodes = this.node?.data || []
    let links = this.link?.data || []
    if (data) {
      var root = hierarchy(data)
      nodes = root.descendants()
      links = root.links()
    }
    this.initData(nodes, links)
  }

  initData (nodes, links) {
    this.nodes = nodes.map(node => {
      const nodeOpts = this.nodeProperty()
      Object.keys(nodeOpts).forEach(key => {
        if (!Object.prototype.hasOwnProperty.call(node, key)) {
          node[key] = nodeOpts[key]
        }
      })
      return node
    })

    this.links = links.map(link => {
      const linkOpts = this.getLinkProperty()

      Object.keys(linkOpts).forEach(key => {
        if (!Object.prototype.hasOwnProperty.call(link, key)) {
          link[key] = linkOpts[key]
        }
      })
      link.relation = link.target.data.name
      return link
    })

    this.simulation = forceSimulation(this.nodes).alphaDecay(0.06).velocityDecay(0.3)
    this.simulation.force("center", forceCenter(this.w / 2, this.h / 2));
    this.simulation.force('manbody', forceManyBody().strength(-100))
    this.simulation.force('link', forceLink(this.links).distance(50))
    this.simulation.force("collide", forceCollide(20))

    let i = 0;
    const render = () => {
      i++
      if (i < 80) {
        if (i > 4) this.getDrawElement()
        requestAnimationFrame(render)
      } else {
        this.simulation.stop()
        this.nodes.forEach(node => {
          node.vx = 0
          node.vy = 0
        })
      }
    }
    render()
  }

  initDom () {
    var zoomFn = zoom().scaleExtent([0.6, 4]).on('zoom', () => {
      this.transform = event.transform
      this.container.attr("transform", this.transform);
      this.getDrawElement()
    })
    this.svg = this.$el.append('svg').style('background-color', '#06101d').style('display', 'block').call(zoomFn)
    this.resize()
    this.container = this.svg.append('g')
    this.svg.call(zoomFn.transform, this.transform)

    this.gLinks = this.container.append('g').classed('links', true)
    this.gNodes = this.container.append('g').classed('nodes', true)

    this.svg.append('defs').append('marker').attr('id', 'arrow-marker-end').attr('markerWidth', 8).attr('markerHeight', 8).attr('refX', 5).attr('refY', 4)
      .attr('viewBox', '0 0 8 8').attr('fill', '#fff').attr('orient', 'auto').append('path').attr('d', 'M1,1L7,4L1,7z')
  }

  getDrawElement () {
    var range = {
      x0: -250,
      y0: -250,
      x1: this.w + 250,
      y1: this.h + 250
    }
    if (this.nodes?.length) {
      this.nodes.forEach(node => {
        const nodeX = this.transform.applyX(node.x)
        const nodeY = this.transform.applyY(node.y)
        node.isDraw = (nodeX > range.x0 && nodeX < range.x1 && nodeY > range.y0 && nodeY < range.y1)
      })
      this.links.forEach(item => {
        const { source, target } = item
        // source和target有一个在显示范围内，这条线段就要绘制出来
        item.isDraw = (source.isDraw || target.isDraw)
      })
      this.updateLinks()
      this.updateNodes()
    }
  }

  updateNodes () {
    const that = this
    var updateSelections = this.gNodes.selectAll('g.g-circle').data(this.nodes.filter(d => !d.hidden && d.isDraw && !d.showOnly), d => d[this._node.key])
    var enterSelections = updateSelections.enter().append('g').classed('g-circle', true)
    enterSelections.append('path').classed('out-circle', true)
    enterSelections.append('path').classed('inner-circle', true)
    enterSelections.append('text').classed('node-text', true).attr('dy', 4).text(d => d.data.name)

    var mergeSelections = updateSelections.merge(enterSelections).attr('opacity', d => d.opacity).attr('transform', d => `translate(${d.x},${d.y})`)
      .on('mouseenter', function (d) {
        const ancestors = d.ancestors().map(item => item[that._node.key])
        // 如果是根节点，不允许操作
        if (d.id !== ancestors[ancestors.length - 1]) {
          const descendants = d.descendants().map(item => item[that._node.key])
          const nodeIds = descendants.concat(ancestors)
  
          that.nodes.forEach(node => {
            node.animation = true
            node.opacity = nodeIds.includes(node[that._node.key]) ? 1 : 0.1
          })
  
          const ancestorLinks = []
          for (var i = ancestors.length - 1; i > 0; i--) {
            ancestorLinks.push({
              source: { id: ancestors[i] },
              target: { id: ancestors[i - 1] }
            })
          }
          const nodeLinks = d.links().concat(ancestorLinks).map(({source, target}) => source.id + '-' + target.id)
  
          that.links.forEach(link => {
            const { source, target } = link
            if (nodeLinks.includes(source.id + '-' + target.id)) {
              link.opacity = 0.8
              link.animation = true
            }
          })
        }

        // 添加 
        select(this).append('path').classed('link-circle', true).attr('id', 'selectPath').attr('d', d => d.path).on('mousedown', (d) => {
          event.stopPropagation()
          let x = that.transform.applyX(d.x)
          let y = that.transform.applyY(d.y)
          that.drawLine({ startx: x, starty: y, source: d })
        })

        that.updateNodes()
        that.updateLinks()
      })
      .on('mouseleave', function (d) {
        select('#selectPath').remove()
        that.nodes.forEach(node => {
          node.opacity = 1
          node.animation = false
        })
        that.links.forEach(link => {
          link.opacity = 0.1
          link.animation = false
        })
        that.updateNodes()
        that.updateLinks()
      })
      .on('click', (d) => {
        this._node.onClick?.(event, d, that)
        this.spread(d)
      })
      .on('dblclick', function (d) {
        event.stopPropagation()
        // d.hiddenChildren = !d.hiddenChildren
        // const linksId = d.links().map(l => {
        //   return l.target.id + '__' + l.source.id
        // })
        // that.links.forEach(link => {
        //   if (linksId.includes(link.target.id + '__' + link.source.id)) {
        //     link.hidden = link.target.parent.hiddenChildren || d.hiddenChildren
        //   }
        // })
        // d.descendants().forEach(node => {
        //   // 排除自己
        //   if (node.id !== d.id) {
        //     node.hidden = node.parent.hiddenChildren || d.hiddenChildren
        //   }
        // })
        // that.updateNodes()
        // that.updateLinks()
      })
      .on('contextmenu', (d) => this._node.onContextmenu?.(event, d, this))
      .call(this.bindDrag())
    mergeSelections.select('.out-circle').attr('fill', d => d.color.outerFill).attr('d', d => d.path).attr('stroke', d => d.color.outerStroke)
    mergeSelections.select('.inner-circle').attr('fill', d => d.color.innerFill).attr('d', d => d.path).attr('stroke', d => d.color.innerStroke)
    mergeSelections.select('.node-text').attr('dx', d => d.r + 5).attr('fill', d => d.color.innerFill).attr('width', function (d) {
      d.nodeTextWidth = this.getComputedTextLength()
      that.setTextCoors(d)
    })


    updateOpc(this.nodes.filter(d => !d.hidden && d.isDraw && !d.showOnly), mergeSelections)

    updateSelections.exit().remove()
  }

  updateLinks () {
    const that = this
    var updateSelections = this.gLinks.selectAll('g.link').data(this.links.filter(d => !d.hidden && d.isDraw && !d.showOnly), d => d[this._link.key])
    var enterSelections = updateSelections.enter().append('g').classed('link', true)
    enterSelections.append('path').classed('link-path', true)
    var gtext = enterSelections.append('g').classed('gtext', true)
    gtext.append('text').classed('link-text', true).text(d => d.relation).attr('dy', 3).attr('width', function (d) {
      d.textWidth = this.getComputedTextLength() + 8
    })
    gtext.insert('rect', 'text').attr('width', d => d.textWidth).attr('height', 16).attr('fill', '#06101d').attr('x', d => -d.textWidth / 2).attr('y', -8)

    var mergeSelection = updateSelections.merge(enterSelections).attr('opacity', d => d.opacity)
      .on('click', function (d) {
        console.log(d)
      })
      .on('mouseenter', (d) => {
        d.opacity = 0.8
        d.animation = true
        this.updateLinks()
      })
      .on('mouseleave', (d) => {
        d.opacity = 0.1
        d.animation = false
        this.updateLinks()
      })

      mergeSelection.select('.link-path').attr('d', (d) => {
        const { source, target } = d
        const { _x, _y } = this.getCos(source, target, target.r + 2)
        return this.generateLinkPath(source, { x: _x, y: _y })
      }).attr('marker-end', d => d.markEnd).classed('link-fade-out', d => d.animation)

      mergeSelection.select('.gtext').style('display', d => d.animation ? 'block':'none').attr('transform', (d) => {
        if (d.animation) {
          const { source, target } = d
          const { _x, _y } = that.getMiddle(source.x, source.y, (target.x + source.x) / 2, source.y ,target.x, target.y)
          const relationRotate = Math.atan((target.y - source.y) / (target.x - source.x)) * 360 / (2 * Math.PI)
          return `translate(${_x},${_y}) rotate(${relationRotate})`
        }
      })
      
    updateSelections.exit().remove()
  }

  setTextCoors (d) {
    const { x, y, nodeTextWidth } = d
    d.textCoors = {
      x0: x + d.r + 5,
      y0: y - 8,
      x1: x + d.r + 5 + nodeTextWidth,
      y1: y + 8
    }
  }

  spread (d) {
    const _nodes = (d.children || []).filter(node => !node.children)

    var _simulation = forceSimulation(_nodes).alphaDecay(0.05).stop()
    _simulation.force("center", forceCenter(d.x, d.y));
    _simulation.force("collide", forceCollide(40))
    for (let i = 0; i < 50; i++) {
      _simulation.tick()
    }
    this.updateNodes()
    this.updateLinks()
    _simulation = null
  }

  drawLine ({ startx, starty, source }) {
    const { transform, generateLinkPath, getCos, simulation, links } = this
    const that = this
    const { k } = transform
    var path = this.svg.insert('path', 'g').classed('link-path', true).attr('marker-end', 'url(#arrow-marker-end)').attr('transform', `scale(${k})`)
    var findNode
    function move (e) {
      const x0 = transform.invertX(e.x)
      const y0 = transform.invertY(e.y)
      const d = simulation.find(x0, y0, 18)
      if (d) {
        const { _x, _y } = getCos(source, d, 12)
        let dx = transform.applyX(_x)
        let dy = transform.applyY(_y)
        findNode = d
        path.attr('d', generateLinkPath({ x: startx / k, y: starty / k }, { x: dx / k, y: dy / k }))
      } else {
        findNode = null
        path.attr('d', generateLinkPath({ x: startx / k, y: starty / k }, { x: e.x / k, y: e.y / k }))
      }
    }
    function mouseup () {
      if (findNode) {
        const linkOpts = that.getLinkProperty()
        links.push({ ...linkOpts, target: findNode, source, relation: source.data.name })
        that.updateLinks()
        simulation.force('link', forceLink(links).distance(30))
      }
      path.remove()
      window.removeEventListener('mousemove', move)
      window.removeEventListener('mouseup', mouseup)
    }
    window.addEventListener('mousemove', move)
    window.addEventListener('mouseup', mouseup)
  }

  bindDrag () {
    const that = this
    function started (d) {
      console.log('start')
      const _nodes = (d.children || []).filter(node => !node.children)
      _nodes.push(d)
      var simulationDrag = forceSimulation(_nodes).alphaDecay(0.05).stop()
      d.simulation = simulationDrag

      simulationDrag.force('manbody', forceManyBody().strength(-100))
      const _links = that.links.filter(({ target, source }) => [target.id, source.id].includes(d.id))
      simulationDrag.force('link', forceLink(_links).distance(60))
      simulationDrag.force("collide", forceCollide(20))
      simulationDrag.on('tick', () => {
        console.log('tick')
        that.getDrawElement()
      })
      simulationDrag.on('end', () => {
        console.log('end')
        simulationDrag = null
        d.fx = null;
        d.fy = null;

      })
      d.fx = d.x;
      d.fy = d.y;
    }
    function dragged (d) {
      d.simulation.alphaTarget(0.3).restart()
      d.fx = event.x;
      d.fy = event.y;
    }
    function ended (d) {
      d.simulation.alphaTarget(0)
    }
    return drag().on("start", started).on("drag", dragged).on("end", ended)
  }

  generateLinkPath (source, target) {
    var path = d3Path();
    path.moveTo(source.x, source.y);
    path.quadraticCurveTo((source.x + target.x) / 2, source.y, target.x, target.y);
    // path.lineTo(source.x, source.y)
    return path.toString();
  }

  getCos (source, target, r) {
    const x = Math.abs(source.x - target.x)
    const y = Math.abs(source.y - target.y)
    const z = Math.sqrt(x * x + y * y)
    const x0 = x * r / z || 0
    const y0 = y * r / z || 0
    let _x, _y
    if (source.x >= target.x) {
      _x = target.x + x0
    } else {
      _x = target.x - x0
    }
    if (source.y >= target.y) {
      _y = target.y + y0
    } else {
      _y = target.y - y0
    }
    return { _x, _y }
  }

  getMiddle (x1, y1, x2, y2, x3, y3) {
    function getCosMid (source, target) {
      const x = Math.abs(source.x - target.x)
      const y = Math.abs(source.y - target.y)
      const z = Math.sqrt(x * x + y * y)
      const x0 = x * 0.5 || 0
      const y0 = y * 0.5 || 0
      let _x, _y
      if (source.x >= target.x) {
        _x = target.x + x0
      } else {
        _x = target.x - x0
      }
      if (source.y >= target.y) {
        _y = target.y + y0
      } else {
        _y = target.y - y0
      }
      return { _x, _y }
    }

    const m_1 = getCosMid({ x: x1, y: y1 }, { x: x2, y: y2 })
    const m_2 = getCosMid({ x: x2, y: y2 }, { x: x3, y: y3 })
    return getCosMid({ x: m_1._x, y: m_1._y }, { x: m_2._x, y: m_2._y })
  }

  getId () {
    return Math.random().toString(16).slice(2) + '-' + Math.random().toString(16).slice(6)
  }
}